import React, { Component } from 'react'


class First extends Component {
  render() {
    return(
      <section className='first'>
                <div class="container">
                    <div class="column">
                       <div class="sides firstSides">
                           <p>We Began Our Journey As Almond Farmers Nearly 70 Years Ago.</p>
                       </div>
                         <div class="heroContainer">
                             <div class="heroImage">
                         </div>
                          </div>
                       <div class="sides firstSides" id='firstRight'>
                           <p>Today We Are A Diversified Business In Agriculture, Retail, And Finance.</p>
                       </div>
                    </div>
                </div>
               </section>
    )
  }
}

export default First