import React, { Component } from 'react'


class Second extends Component {
  render() {
    return(
      <section className='second'>
                <div class="containerCenter">
                    <div>
                        <div class="center">
                            <p>At our core, we're a family company. We began our journey as almond farmers nearly 70 years ago in the Central Valley of California. Today, we have built on that humble foundation, to become a diversified business with targeted activities in agriculture, retail, and finance.  Though much as changed over the last century, one thing has not changed for us: focusing on creating value for our employees, our customers, and our communities.</p>
                        </div>
                    </div>
                </div>
               </section >
    )
  }
}

export default Second