import React, { Component } from 'react'
import Heart from '../../Assets/WhiteHeart.svg'
import Science from '../../Assets/Science.svg'
import Seeking from '../../Assets/Seeking.svg'
import Orchard from '../../Assets/Orchard.jpg'


class Third extends Component {
  render() {
    return(
      <section className="third">
                <div class="container">
                    <div class="row">
                        <div class="rows">
                            <div class="icon">
                                <img src={Heart} alt="Heart" class="heart"/>
                            </div>
                            <div class="rowContent">
                            <div class="rowContent">
                            <h3>Creating Value for <br/>the Customer</h3>
                                <p>Every aspect of an organization should be focused on creating value for the customer. It is helpful to consider this true-north concept that should guide decision making and continuous improvement. An organization should drive all aspects of value, including quality, cost, delivery, safety and morale.</p>
                            </div>
                            </div>
                        </div>
                        <div class="rows">
                            <div class="icon">
                                <img src={Science} alt="Human Mind" class="mind"/>
                            </div>
                            <div class="rowContent">
                            <div class="rowContent">
                            <h3>Seeking <br/>Perfection</h3>
                                <p>One should always look for problems where there doesn’t appear to be any. This is contrary to the traditional belief: “If it isn’t broken, don’t fix it.” The pursuit of perfection reveals that there are always opportunities for improvement..</p>
                            </div>
                            </div>
                        </div>
                        <div class="rows">
                            <div class="icon">
                                <img src={Seeking} alt="Person Meditating, seeking perfection" class="seeking"/>
                            </div>
                            <div class="rowContent">
                                <h3>Embrace Scientific <br/>thinking</h3>
                                <p>All associates can be trained to use scientific thinking to improve the processes with which they work, creating a culture that provides common understanding, approach and language regarding improvement.</p>
                            </div>
                        </div>
                    </div>
                </div>
               </section>
    )
  }
}

export default Third